<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png" class="logo">
    <p class="welcome-text">프렌드 링크!</p> <!-- 중간에 글씨 추가 -->
    <HelloWorld msg="Welcome to Your Vue.js App" />
    <input v-model="nickname" placeholder="대화명을 입력하세요" class="nickname-input" />
    <button @click="enterRoom" class="enter-button">대화방 입장</button>
    <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nickname: '',
      errorMessage: '',
    };
  },
  methods: {
    enterRoom() {
      if (!this.nickname) {
        this.errorMessage = '대화명을 입력해 주세요.';
      } else {
        // 중복 체크 및 방으로 이동
        this.$router.push({ name: 'Room', params: { nickname: this.nickname } });
      }
    },
  },
};
</script>

