<template>
  <div class="container">
    <h1>Topic Room</h1>
    <h2>대화명: {{ nickname }}</h2>
    
    <div v-for="(question, index) in questions" :key="index" class="question-container">
      <h3>{{ question.text }}</h3>
      <div class="button-container">
        <button @click="selectOption(index, 'A')" :disabled="isSelected(index)" :class="['option-button', isSelected(index) ? 'disabled' : '']">A</button>
        <span class="result">{{ results[index] === 'A' ? '선택: A' : results[index] === 'B' ? '선택: B' : '' }}</span>
        <button @click="selectOption(index, 'B')" :disabled="isSelected(index)" :class="['option-button', isSelected(index) ? 'disabled' : '']">B</button>
      </div>
    </div>
    
    <button class="show-results" @click="showResults" :disabled="resultsSubmitted">결과 보기</button>
    
    <div v-if="showingResults" class="results-container">
      <h3>선택 결과</h3>
      <div v-for="(result, index) in aggregatedResults" :key="index">
        <p>질문 {{ index + 1 }}: A 선택 - {{ result.A }}회, B 선택 - {{ result.B }}회</p>
      </div>
      <p>당신의 선택: {{ finalSentence }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nickname: this.$route.params.nickname,
      questions: [
        { text: 'A.남자 VS 여자 B.' },
        { text: 'A.얼굴 VS 몸매 B.' },
        { text: 'A.사랑 VS 우정 B.' },
        { text: 'A.종이책 VS 전자책 B.' },
        { text: 'A.밤 VS 낮 B.' },
        { text: 'A.진보 VS 보수 B.' },
        { text: 'A.여름 VS 겨울 B.' },
        { text: 'A.소주 VS 맥주 B.' },
        { text: 'A.축구 VS 농구 B.' },
        { text: 'A.발라드 VS 락 B.' },
      ],
      results: Array(10).fill(''),
      aggregatedResults: Array(10).fill(null).map(() => ({ A: 0, B: 0 })),
      showingResults: false,
      finalSentence: '',
      resultsSubmitted: false,
      predefinedSentences: [
        "당신은 해맑아 보이지만 행복과 사랑으로 넘쳐나는 사람입니다.",
        "당신은 고민이 많아 보이지만 사려가 깊고 배려가 넘치는 사람입니다.",
        "당신은 긴장한 듯 보이지만 늘 겸손하고 경청할 줄 아는 사람입니다.",
        "당신은 자신을 낮추지만 그 누구보다도 성실하고 근면한 사람입니다.",
        "당신은 긍정적이고 밝은 에너지를 발산하는 사람입니다.",
        "당신은 언제나 주변 사람들을 배려하는 따뜻한 사람입니다.",
        "당신은 도전적인 성격으로 새로운 것을 추구하는 사람입니다.",
        "당신은 항상 정직하고 신뢰할 수 있는 사람입니다.",
        "당신은 세심하고 세련된 감각을 가진 사람입니다.",
        "당신은 사랑과 우정을 중요시하는 사람입니다."
      ],
    };
  },
  methods: {
    async selectOption(index, option) {
      if (this.results[index] === '') {
        this.results[index] = option; 
        this.aggregateResults(index, option);
      } else {
        alert("이미 선택한 질문입니다.");
      }
    },
    async fetchResults() {
      const response = await fetch('http://localhost:3000/api/votes');
      if (response.ok) {
        const data = await response.json();
        this.aggregatedResults = Object.keys(data).map((key) => ({
          A: data[key].optionA,
          B: data[key].optionB,
        }));
      } else {
        console.error('투표 결과 가져오기 실패');
      }
    },
    startPolling() {
      setInterval(this.fetchResults, 5000);
    },
    isSelected(index) {
      return this.results[index] !== '';
    },
    showResults() {
      if (this.results.every(result => result !== '')) {
        this.showingResults = true;
        this.finalSentence = this.getRandomSentence();
        this.resultsSubmitted = true;
      } else {
        alert("모든 질문에 답변해야 결과를 볼 수 있습니다.");
      }
    },
    aggregateResults(index, option) {
      if (index < 0 || index >= this.aggregatedResults.length) {
        console.error('잘못된 인덱스:', index);
        return;
      }
      if (option === 'A') {
        this.aggregatedResults[index].A += 1;
      } else if (option === 'B') {
        this.aggregatedResults[index].B += 1;
      }
    },
    getRandomSentence() {
      return this.predefinedSentences[Math.floor(Math.random() * this.predefinedSentences.length)];
    },
    resetResults() {
      this.results = Array(10).fill(''); // 결과 초기화
      this.resultsSubmitted = false; // 버튼 활성화
    },
    resetResultsIfNeeded() {
      const lastSavedDate = localStorage.getItem('lastSavedDate');
      const currentDate = new Date().toLocaleDateString();

      // 날짜가 다르면 결과 초기화
      if (lastSavedDate !== currentDate) {
        this.resetResults();
        localStorage.setItem('lastSavedDate', currentDate);
      } else {
        // 이전 결과를 사용하지 않음
        this.results = Array(10).fill(''); // 결과 초기화
      }
    }
  },
  created() {
    this.nickname = this.$route.params.nickname;
    this.resetResults(); // 대화방에 들어올 때 항상 결과 초기화
    this.resetResultsIfNeeded();
    this.fetchResults();
    this.startPolling();
  },
};
</script>

<style scoped>
.container {
  max-width: 600px;
  margin: auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.question-container {
  margin-bottom: 20px;
}

.button-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.option-button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.option-button:hover:not(.disabled) {
  background-color: #007bff;
  color: white;
}

.disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.result {
  margin: 0 10px;
  font-weight: bold;
}

.show-results {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.show-results:hover:not(:disabled) {
  background-color: #218838;
}

.results-container {
  margin-top: 20px;
  padding: 10px;
  background-color: #e9ecef;
  border-radius: 5px;
}
</style>
